@import url('https://fonts.googleapis.com/css?family=Do+Hyeon');

.box {
  border-top: 1px solid black;
  border-bottom: 1px solid black;
  margin-bottom: 15px;
}

h4{
  font-family: Do Hyeon;
  font-weight: 500;
  margin: 0;
  margin-left: 10px;
  margin-top: 5px ;
}

.list{
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-line-pack: justify;
      align-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  margin-top: 5px;
}

.img {
  height: 100px;
  width: 140px;
  display: inline;
}

.card{
  height: 160px;
  width: 140px;
  margin: 5px 15px;
  cursor: pointer;
}



