.input {
  border: none;
  font-size: 15px;
  font-weight: 600;
  border-bottom: 2px solid #681e07;
  outline: none;
  padding: 10px;
  width: 85%;
  display: inline;
  background-color: inherit;
}

.button{
  outline: none;
  font-size: 15px;
  display: inline;
  margin-left: 0px;
  text-align: center;
  border-radius: 2em;
  height: 30px;
  font-weight: 700;
  letter-spacing: 1px;
  background-color: white;
  border: 2px solid #681e07;
  color: #681e07;
  width: 55px;
}

.button:hover{
  background-color: #681e07;
  color: white;
  -webkit-box-shadow: 0px 15px 20px rgba(104, 30, 7, 0.4);
          box-shadow: 0px 15px 20px rgba(104, 30, 7, 0.4);
  -webkit-transform: translateY(-2px);
      -ms-transform: translateY(-2px);
          transform: translateY(-2px);
}

.button:active{
  -webkit-transform: translateY(3px);
      -ms-transform: translateY(3px);
          transform: translateY(3px);
}



